:root {
  font-size: 62.5%; }

* {
  box-sizing: border-box; }

.wrapper {
  background: url("/dist/../img/bg.jpg") no-repeat center;
  background-size: cover;
  font-family: "Asap", sans-serif;
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 90px 180px 350px 450px; }

.header {
  width: 100%;
  display: grid;
  align-items: center;
  text-align: center;
  background: #FCB97D;
  position: relative; }

.header__time {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1.3rem; }

.search {
  text-align: center;
  background: rgba(0, 0, 0, 0.6);
  padding: 70px 0; }

.current {
  font-size: 1.3rem;
  padding: 30px;
  border-bottom: 3px solid #FCB97D; }

.current__h2,
.forecast__h2 {
  text-transform: uppercase;
  position: relative; }

.current__h2::after,
.forecast__h2::after {
  content: '';
  position: absolute;
  width: 20%;
  max-width: 100px;
  height: 2px;
  background: #F7FFF7;
  position: absolute;
  left: 20px;
  bottom: -5px; }

.details {
  width: 80%;
  margin: auto;
  text-align: center;
  padding: 10px 0; }

.details__weather {
  font-size: 2.5rem;
  text-transform: capitalize;
  margin-top: -10px; }

.icon__img {
  margin-top: -30px;
  width: 40%;
  max-width: 200px; }

.details__temp {
  font-size: 2.5rem;
  font-family: "Merriweather Sans", sans-serif;
  color: #F7FFF7; }

.forecast {
  padding: 0 20px;
  font-size: 1.3rem;
  overflow-x: hidden;
  position: relative; }

.cannotLeft,
.cannotRight {
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  transform: translateY(-50%) rotateZ(-90deg);
  top: 50%;
  width: 120px;
  text-align: center;
  padding: 5px 0;
  color: #F7FFF7;
  transition: transform 0.1s ease-out; }

.cannotLeft {
  left: -40px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transform: translateX(-100%); }

.showLeft {
  transform: translateY(-50%) rotateZ(-90deg) translateX(0); }

.cannotRight {
  right: -40px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  transform: translateX(100%); }

.showRight {
  transform: translateY(-50%) rotateZ(-90deg) translateX(0); }

@-moz-document url-prefix() {
  .forecast {
    padding: 50px 20px; } }

.day-wrapper {
  width: 500%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  transition: opacity 0.5s ease-in 0.3s, transform 0.1s ease-out; }

.visible {
  opacity: 1; }

.item {
  width: 90%;
  max-width: 400px;
  margin: auto;
  background: rgba(98, 131, 149, 0.6);
  padding: 20px 20px 1px;
  border-top-left-radius: 30px; }

.info__day-name {
  font-size: 1.7rem;
  color: #EDD892;
  margin-bottom: 10px;
  text-align: right; }

.info__date {
  text-align: right;
  font-family: "Merriweather Sans", sans-serif;
  color: #F7FFF7; }

.item__day,
.item__night {
  text-align: center;
  margin: 10px 0; }

.item__day {
  background: url("/dist/../icons/day.svg") no-repeat center;
  background-size: cover; }

.item__night {
  background: url("/dist/../icons/night.svg") no-repeat center;
  background-size: cover; }

.item__phase {
  text-transform: uppercase; }

.search__input {
  width: 80%;
  padding: 10px 0;
  font-size: 1.7rem;
  outline: none;
  box-shadow: none;
  border: none;
  border-bottom: 3px solid #628395;
  background: transparent;
  color: #F7FFF7;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  transition: border 0.5s ease-in; }

.search__input::-webkit-input-placeholder {
  color: #F7FFF7;
  transition: transform 0.3s; }

.search__input::-moz-placeholder {
  color: #F7FFF7;
  transition: transform 0.3s; }

.search__input:-ms-input-placeholder {
  color: #F7FFF7;
  transition: transform 0.3s; }

.search__input::-ms-input-placeholder {
  color: #F7FFF7;
  transition: transform 0.3s; }

.search__input::placeholder {
  color: #F7FFF7;
  transition: transform 0.3s; }

.search__input:focus {
  border-bottom-color: #EDD892; }

.search__input:focus::-webkit-input-placeholder {
  transform: translateY(100%) scale(0); }

.search__input:focus::-moz-placeholder {
  transform: translateY(100%) scale(0); }

.search__input:focus:-ms-input-placeholder {
  transform: translateY(100%) scale(0); }

.search__input:focus::-ms-input-placeholder {
  transform: translateY(100%) scale(0); }

.search__input:focus::placeholder {
  transform: translateY(100%) scale(0); }

.footer {
  background: #96897B;
  padding: 20px;
  text-align: center;
  font-family: "Asap", sans-serif;
  font-size: 1.3rem; }

.footer__name {
  font-family: "Merriweather Sans", sans-serif;
  font-weight: bold; }

@media (min-width: 679px) {
  .wrapper {
    grid-auto-rows: 140px 180px 350px 450px; }
  .header__time {
    font-size: 1.7rem; }
  .search__input {
    width: 40%;
    max-width: 600px; } }

@media (min-width: 800px) and (max-width: 1399px) {
  .wrapper {
    grid-auto-rows: 140px 180px 350px 1250px; }
  .forecast {
    overflow: hidden; }
  .day-wrapper {
    width: 100%;
    grid-row-gap: 30px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr; }
  .item:nth-of-type(5) {
    grid-column: 1 / 3; } }

@media (min-width: 1400px) {
  .wrapper {
    grid-auto-rows: 140px auto 350px 550px;
    background: none; }
  .search {
    background: url("/dist/../img/search.jpg") no-repeat center;
    background-size: cover;
    position: relative;
    z-index: 0;
    padding: 120px 0; }
    .search::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.6);
      z-index: -1; }
  .header__h1 {
    font-size: 2.5rem; }
  .current {
    background: #F7FFF7; }
  .current__h2,
  .forecast__h2 {
    font-size: 2.5rem; }
  .details__weather {
    margin-top: -45px;
    font-size: 3.4rem; }
  .details__temp {
    color: #628395; }
  .forecast {
    font-size: 1.7rem;
    background: url("/dist/../img/bg.jpg") no-repeat center;
    background-size: cover; }
  .day-wrapper {
    width: 100%;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr; }
  .info__day-name {
    font-size: 2.5rem; } }
